<template>
    <v-container>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{verifyYourPasswordText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card class="pa-5" style="margin-top: 60px;">
            <v-card-text class="pa-0" align="justify" style="margin-bottom: 20px">
                {{introduceYourPasswordToContinueText}}
            </v-card-text>
            <v-form v-model="validForm">
                <v-text-field
                        v-model="password"
                        :rules="[rules.required, rules.min8Char]"
                        :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="showPassword ? 'text' : 'password'"
                        :label="passwordText"
                        :hint="min8CharText"
                        counter
                        @click:append="showPassword = !showPassword"
                        required
                ></v-text-field>
            </v-form>

            <v-btn block color="primary" style="margin-top: 20px" @click="verifyPassword" :disabled="!validForm">
                {{continueText}}
            </v-btn>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from "vuex";

    export default {
        name: "SettingsAccountPasswordVerification",
        data() {
            return {
                validForm: false,
                snackbar: false,
                password: '',
                snackbarText: null,
                showPassword: false,
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD'),
                    min8Char: value => (value.length >= 8 || value.length == 0) || this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD')
},
                email: this.$store.getters['user/getEmail'],
                passwordText: null,
                closeText: null,
                continueText: null,
                min8CharText: null,
                verifyYourPasswordText: null,
                introduceYourPasswordToContinueText: null
}
        },
        methods: {
            ...mapActions('user', ['login']),
            verifyPassword: function () {
                let self = this;
                let resultLogin = self.login({
                    email: self.email,
                    password: self.password
                });

                resultLogin.then(() => {
                    self.$router.push({name: 'settingsAccountEditEmail'});
                })
                    .catch(function (error) {
                        if (error == this.$i18n.t('messages.ERROR_WRONG_EMAIL_PASSWORD_COMBINATION')) {
                            self.snackbarText = this.$i18n.t('messages.ERROR_TEXT_WRONG_PASSWORD');
                        } else {
                            self.snackbarText = error;
                        }

                        self.snackbar = true;
                    });
            }
        },
        created() {
            this.passwordText = this.$i18n.t('messages.TEXT_PASSWORD');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.continueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
            this.min8CharText = this.$i18n.t('messages.RULE_TEXT_MIN_VALUE_CHARACTERS_PASSWORD');
            this.verifyYourPasswordText = this.$i18n.t('messages.TEXT_VERIFY_YOUR_PASSWORD');
            this.introduceYourPasswordToContinueText = this.$i18n.t('messages.TEXT_INTRODUCE_YOUR_PASSWORD_TO_CONTINUE');

        }
    }
</script>

<style scoped>

</style>